.logo {
  position: relative;
  height: 100%;
  box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.5);
  border-radius: 20px;
  background-color: white; }
  .logo img {
    width: 90%;
    height: 90%;
    object-fit: contain; }

.infobar {
  position: relative;
  height: 100%;
  border-radius: 10px;
  font-size: 3vh;
  background-color: #1d99d5; }
  .infobar--item {
    color: white;
    text-transform: uppercase; }

.main {
  margin: 2vh;
  height: 96vh;
  display: grid;
  grid-template-columns: 1fr 5fr 2fr;
  grid-template-rows: 1fr 5fr 6fr;
  grid-template-areas: "info info ports" "logos coms ports" "table table ports";
  grid-gap: 5vh; }

.infobar {
  grid-area: info;
  margin-left: 8vw; }

.logos {
  grid-area: logos;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr 1fr;
  grid-gap: 5vh; }

.coms {
  grid-area: coms;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr;
  grid-gap: 5vh; }

.table {
  grid-area: table;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: repeat(2, 1fr);
  grid-gap: 1vh; }

.ports {
  grid-area: ports;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 11fr 6fr;
  grid-gap: 1vh; }

body {
  margin: 0;
  font-family: "Raleway", Verdana, Geneva, Tahoma, sans-serif;
  background-image: -webkit-linear-gradient(left, #b1e9ff, white);
  background-image: linear-gradient(to right, #b1e9ff, white); }

button {
  border: 0; }
  button:focus {
    outline: 0; }

.center {
  position: absolute;
  text-align: center;
  top: 50%;
  left: 50%;
  width: 100%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%); }

.center-horizontal {
  position: absolute;
  text-align: center;
  left: 50%;
  width: 100%;
  -webkit-transform: translate(-50%, 0%);
          transform: translate(-50%, 0%); }

.center-vertical {
  position: absolute;
  text-align: center;
  top: 50%;
  -webkit-transform: translate(0%, -50%);
          transform: translate(0%, -50%); }

